&.foreningen {

    section:not(.instagram-feed) {
        display: flex;
        margin: 50px 0;

        img {
            max-height: 600px;
            padding: 0 50px 0 0;
            object-fit: cover;
            width: 50%;
        }
        &:nth-of-type(2n) {
            img {
                order: 1;
                padding: 0 0 0 50px;              
            }
        }
        .text {
            padding: 2em;
            background-color: rgba($color__interactive, .15);
            width: 50%;
            border-radius: 10px;
        }
    }
}