.posts-container {
    width: 55%;
    float: left;

    .stf-post {
        margin-top: 50px;
        padding-bottom: 30px;
        top: 0;


        img {
            object-fit: cover;
            width: 100%;
            max-height: 300px;
        }
        .date {
            position: relative;
            margin: 0;
            float: right;
        }
        .entry-footer {
            margin: 5px 0 0 0;
        }
        .text {
            text-align: left;
            color: $color__text-main;
            
            .date {
                font-weight: bold;
                font-family: $font__averia;
            }
            h5 {
                margin-top: 0;
                margin-bottom: 0;
            }
        }
        p {
            font-size: 18px;
            margin-bottom: 0;
            margin-top: 0;
            color: #787878;
        }
    }
}

@import 'post';