/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden],
template {
  display: none;
}

a {
  background-color: transparent;
}

a:active,
a:hover {
  outline: 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

dfn {
  font-style: italic;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

mark {
  background: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
}

pre {
  overflow: auto;
}

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

button {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input {
  line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
}

optgroup {
  font-weight: bold;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
  color: #000;
  font-family: "Averia Sans Libre";
  font-size: 18px;
  font-size: 1.125rem;
  line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: .75em;
  margin-bottom: .25em;
  font-family: "Averia Sans Libre";
  clear: both;
}

h1 {
  font-size: 60px;
  font-size: 3.75rem;
}

h2 {
  font-size: 52px;
  font-size: 3.25rem;
}

h3 {
  font-size: 44px;
  font-size: 2.75rem;
}

h4 {
  font-size: 36px;
  font-size: 2.25rem;
}

h5 {
  font-size: 28px;
  font-size: 1.75rem;
}

h6 {
  font-size: 20px;
  font-size: 1.25rem;
}

p {
  font-family: 'Open Sans';
  margin-top: .5em;
  margin-bottom: 1em;
}

dfn, cite, em, i {
  font-style: italic;
}

blockquote {
  position: relative;
  margin: 0 0 1em;
  background: #eee;
  padding: 1em 2em .5em 5em;
  font-style: italic;
}

blockquote:before {
  position: absolute;
  top: -.2em;
  left: -.13em;
  content: "“";
  font-size: 12em;
  line-height: 100%;
  color: white;
}

address {
  margin: 0 0 1.5em;
}

pre {
  background: #eee;
  font-family: "Courier 10 Pitch", Courier, monospace;
  font-size: 18.75px;
  font-size: 1.17188rem;
  line-height: 1.6;
  margin-bottom: 1.6em;
  max-width: 100%;
  overflow: auto;
  padding: 1.6em;
}

code, kbd, tt, var {
  font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
  font-size: 18.75px;
  font-size: 1.17188rem;
}

abbr, acronym {
  border-bottom: 1px dotted #666;
  cursor: help;
}

mark, ins {
  background: #fff9c0;
  text-decoration: none;
}

big {
  font-size: 125%;
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit;
}

body {
  background-size: 412px;
}

blockquote, q {
  quotes: "" "";
}

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5em;
}

ul,
ol {
  margin: 0 0 1.5em;
}

ul {
  list-style: disc;
}

ol {
  list-style: decimal;
}

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: .5em;
}

dt {
  font-weight: bold;
}

dd {
  margin: 0 1.5em 1.5em;
}

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */
}

figure {
  margin: 0;
  /* Extra wide images within figure tags don't overflow the content area. */
}

table {
  border-collapse: collapse;
  margin: 0 0 1.5em;
  width: 100%;
}

thread th {
  border-bottom: 2px solid #bbb;
  padding-bottom: .5em;
}

td,
th {
  vertical-align: top;
}

th {
  padding: .4em;
  text-align: left;
}

tr {
  border-bottom: 1px solid #eee;
}

td {
  padding: .4em;
}

th:first-child,
td:first-child {
  padding-left: 0;
}

th:last-child,
td:last-child {
  padding-right: 0;
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: 2px solid;
  border-color: black;
  background: transparent;
  color: black;
  font-size: 20px;
  font-size: 1.25rem;
  line-height: 1;
  padding: .5em 1em;
}

button:hover, button:active, button:focus,
input[type="button"]:hover,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:hover,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:hover,
input[type="submit"]:active,
input[type="submit"]:focus {
  color: black;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
  color: #666;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 3px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
  color: #111;
}

select {
  border: 1px solid #ccc;
}

textarea {
  width: 100%;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
  text-decoration: none;
}

a:hover, a:focus, a:active {
  color: #41CA86;
  text-decoration: underline;
}

a:focus {
  outline: thin dotted;
}

a:hover, a:active {
  outline: 0;
}

.page-content a,
.entry-content a,
.entry-summary a,
.comment-content a {
  text-decoration: none;
}

.page-content a:focus, .page-content a:hover,
.entry-content a:focus,
.entry-content a:hover,
.entry-summary a:focus,
.entry-summary a:hover,
.comment-content a:focus,
.comment-content a:hover {
  color: #41CA86;
}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
  margin: 0 0 1.5em;
  overflow: hidden;
}

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
  float: left;
  width: 50%;
}

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
  float: right;
  text-align: right;
  width: 50%;
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #000;
  display: block;
  font-size: 17.5px;
  font-size: 1.09375rem;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
  outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5em;
}

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5em;
}

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
  content: "";
  display: table;
  table-layout: fixed;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-content:after,
.site-footer:after {
  clear: both;
}

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Global layouts
--------------------------------------------------------------*/
.site-content {
  max-width: 1200px;
  margin: auto;
  margin-bottom: 50px;
}

@media screen and (min-width: 480px) {
  .site-content {
    margin-top: 50px;
  }
}

/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
.site-header {
  position: relative;
  background-color: #f2f2f2;
}

.top-header {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1em 0;
  color: black;
  display: flex;
  justify-content: space-between;
}

.top-header .site-branding .custom-logo-link {
  display: block;
  overflow: auto;
  width: 20%;
  float: left;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  padding-right: 1em;
}

.top-header .site-branding .custom-logo-link img {
  height: 100%;
  min-width: 100%;
}

.top-header .site-branding .text {
  width: 80%;
  float: left;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  vertical-align: top;
}

.top-header .site-branding .text .site-title {
  margin: 0;
  padding: 0;
  font-weight: bold;
  line-height: 1.2;
}

.top-header .site-branding .text .site-description {
  margin: 0;
  padding-top: 2%;
  font-style: italic;
  font-weight: 100;
  display: inline-block;
}

.top-header .contact-header {
  display: flex;
  align-items: center;
  margin-left: 20px;
}

.top-header .contact-header a {
  color: black;
  position: relative;
  font-size: 1.2em;
}

.top-header .contact-header a span {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: -47px;
  font-size: 30px;
  background-color: #002254;
  padding: 6px;
  border-radius: 4px;
  color: white;
}

@media screen and (max-width: 480px) {
  .top-header {
    display: none;
  }
}

.main-navigation {
  display: block;
  font-weight: 400;
  clear: left;
  background-color: #002254;
}

@media screen and (max-width: 480px) {
  .main-navigation {
    z-index: 100;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
  }
}

.main-navigation .mobile-navbar {
  display: none;
  height: 100%;
}

@media screen and (max-width: 480px) {
  .main-navigation .mobile-navbar {
    display: flex;
    justify-content: space-between;
  }
}

.main-navigation .mobile-navbar .custom-logo-link {
  padding: 0 0 0 .8em;
  width: 60px;
}

@media screen and (min-width: 480px) {
  .main-navigation .mobile-navbar .custom-logo-link {
    display: none;
  }
}

.main-navigation .mobile-navbar .custom-logo-link img {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  width: auto;
}

.main-navigation .mobile-navbar .menu-toggle {
  outline: none;
  font-size: 18px;
  display: none;
  padding: 0 .8em;
  text-transform: uppercase;
  color: #fff;
  border: none;
}

@media screen and (max-width: 480px) {
  .main-navigation .mobile-navbar .menu-toggle {
    display: block;
  }
}

.main-navigation .mobile-navbar .menu-toggle:hover {
  color: #fff;
}

.main-navigation.toggled .menu {
  display: flex;
}

@media screen and (min-width: 480px) {
  .main-navigation .menu-navigation-container {
    max-width: 1200px;
    margin: auto;
  }
}

.main-navigation .menu {
  padding: 0;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
}

@media screen and (max-width: 480px) {
  .main-navigation .menu {
    display: none;
    height: calc(100vh - 60px);
    background-color: #002254;
    flex-direction: column;
    justify-content: center;
  }
}

.main-navigation .menu .menu-item {
  position: relative;
}

.main-navigation .menu .menu-item.current-menu-item, .main-navigation .menu .menu-item.current_page_parent {
  background-color: #41CA86;
}

@media screen and (min-width: 480px) {
  .main-navigation .menu .menu-item.current-menu-parent {
    background-color: #41CA86;
  }
}

@media screen and (min-width: 480px) {
  .main-navigation .menu .menu-item.cta {
    margin-left: 1.5em;
  }
}

.main-navigation .menu .menu-item.cta a {
  border-radius: 5px;
  padding: 0.5em 1.5em;
  text-transform: uppercase;
}

@media screen and (min-width: 480px) {
  .main-navigation .menu .menu-item.cta a {
    background-color: #41CA86;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
  }
}

.main-navigation .menu .menu-item.cta a:hover {
  text-decoration: none;
  opacity: 0.8;
}

.main-navigation .menu .menu-item a {
  font-size: 16px;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-family: "Open Sans";
  font-weight: bold;
  transition: background-color .15s ease-out;
  display: inline-block;
  width: 100%;
  padding: 1.2em 1.5em;
  text-decoration: none;
  color: #fff;
}

@media screen and (max-width: 480px) {
  .main-navigation .menu .menu-item a {
    text-align: center;
    padding: .8em 0;
  }
}

.main-navigation .menu .menu-item a:after {
  content: ' +';
}

@media screen and (min-width: 480px) {
  .main-navigation .menu .menu-item a:hover {
    background-color: #41CA86;
  }
}

.main-navigation .menu .menu-item a:only-child:after {
  content: '';
}

.main-navigation .menu .menu-item .menu-toggle {
  display: none;
}

.main-navigation .menu .menu-item.menu-item-has-children .sub-menu {
  transform-origin: top;
  transition: transform .2s ease, opacity .2s ease;
  margin: 0;
  padding: 0;
  list-style-type: none;
  z-index: 1;
  background-color: #002254;
  top: 100%;
  left: 0;
}

@media screen and (min-width: 480px) {
  .main-navigation .menu .menu-item.menu-item-has-children .sub-menu {
    position: absolute;
    transform: scaleY(0);
    opacity: 0;
  }
}

@media screen and (max-width: 480px) {
  .main-navigation .menu .menu-item.menu-item-has-children .sub-menu {
    display: none;
  }
}

.main-navigation .menu .menu-item.menu-item-has-children.toggle .sub-menu {
  display: block;
  transform: scaleY(1);
  opacity: 1;
}

@media screen and (min-width: 480px) {
  .main-navigation .menu .menu-item:hover .sub-menu {
    transform: scaleY(1);
    opacity: 1;
  }
}

.main-navigation.stick {
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
.site-footer {
  position: relative;
  font-size: .8em;
  font-family: "Averia Sans Libre";
  color: #fff;
  background-color: #08082E;
  text-align: center;
}

.site-footer a {
  transition: color 0.15s ease-out;
  color: #fff;
  text-decoration: none;
}

.site-footer a:focus, .site-footer a:hover {
  color: #41CA86;
}

.site-footer .container {
  padding: 5em 0;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.site-footer .social-menu {
  width: 180px;
}

.site-footer .social-menu ul {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.site-footer .social-menu ul li {
  width: 100%;
}

.site-footer .social-menu ul li:first-child a::before {
  content: " ";
  font-family: 'fontawesome';
}

.site-footer .social-menu ul li:last-child a::before {
  content: " ";
  font-family: 'fontawesome';
}

.site-footer .social-menu .fa::before {
  pointer-events: none;
  margin-top: 8px;
  font-size: 2em;
  float: left;
  width: 30px;
}

.site-footer .social-menu a {
  padding: .5em 0 .5em 1.2em;
  font-family: "Averia Sans Libre";
  font-size: 1.5em;
  display: block;
  padding: .5em 1em;
  text-decoration: none;
}

.site-footer .site-info {
  font-family: "Open Sans";
  font-size: 12px;
  color: #555555;
  padding: 1em 0;
  background-color: black;
}

.site-footer .site-info a {
  font-weight: bold;
  color: dimgray;
}

.site-footer .site-info a:hover {
  text-decoration: underline;
}

.site-footer .donate {
  font-size: 1.4em;
}

.site-footer .email {
  order: 1;
  text-align: right;
  font-size: 1.4em;
}

.site-footer .email a {
  position: relative;
}

.site-footer .email a span {
  position: absolute;
  left: -40px;
  font-size: 30px;
  margin-right: 10px;
}

@media screen and (max-width: 480px) {
  .site-footer .container {
    padding: 1em;
    flex-wrap: wrap;
  }
  .site-footer .container > * {
    margin: 20px 0;
  }
  .site-footer .container .social-menu {
    order: 2;
    font-size: 14px;
    font-size: 0.875rem;
  }
  .site-footer .container .email {
    order: 3;
    font-size: 20px;
    font-size: 1.25rem;
  }
  .site-footer .container .donate {
    order: 1;
    width: 100%;
  }
  .site-footer .site-info {
    padding-left: 15%;
    padding-right: 15%;
  }
}

/*--------------------------------------------------------------
## Sidebar
--------------------------------------------------------------*/
.side-archive {
  width: 30%;
  background-color: #fff;
  float: right;
  padding: 0 50px;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

@media screen and (min-width: 480px) {
  .side-archive {
    margin: 50px 0;
  }
}

