/*--------------------------------------------------------------
## Slider
--------------------------------------------------------------*/
.stf-slider {
  flex: 50%;
  display: block;
  position: relative;
  margin-right: 1em;
  width: 100%;
  overflow: hidden;
}

.stf-slider ul {
  position: relative;
  left: 0;
  margin: 0;
  padding: 0;
  list-style-type: none;
  height: 100%;
}

@media screen and (min-width: 480px) {
  .stf-slider ul {
    transition: left .8s ease-out;
  }
}

@media screen and (max-width: 480px) {
  .stf-slider ul {
    transition: left .4s ease-out;
  }
}

.stf-slider ul .slide {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  float: left;
  height: 100%;
}

.stf-slider ul .slide img {
  object-fit: cover;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  min-height: 100%;
  width: 100%;
}

.stf-slider ul .slide div {
  opacity: 0;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 70%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.stf-slider ul .slide div.current {
  animation-name: slide;
  animation-duration: 600ms;
  animation-timing-function: ease-out;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: 1s;
  transform-origin: bottom;
}

.stf-slider ul .slide div h4,
.stf-slider ul .slide div p {
  color: #fff;
  text-shadow: 1px 1px 1px black;
}

.stf-slider ul .slide div p {
  margin-bottom: 2em;
}

.stf-slider ul .slide div a {
  color: #fff;
  background-color: #41CA86;
  border-radius: 5px;
  padding: .5em 1.5em;
  text-transform: uppercase;
  font-size: 18px;
  box-shadow: 1px 1px 1px black;
}

.stf-slider .previous,
.stf-slider .next {
  outline: none;
  color: white;
  font-size: 1.5em;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: 0;
  border: none;
  height: 100%;
  padding: 0 2%;
}

@media screen and (min-width: 480px) {
  .stf-slider .previous,
  .stf-slider .next {
    transition: color 150ms ease-out;
  }
  .stf-slider .previous:hover,
  .stf-slider .next:hover {
    color: #41CA86;
  }
}

.stf-slider .previous {
  left: 0;
}

.stf-slider .next {
  right: 0;
}

@keyframes slide {
  40% {
    opacity: 0;
    transform: scaleY(0);
  }
  100% {
    opacity: 1;
    transform: scaleY(1);
  }
}

